/* ==========================================================================
   Main Navigation
   ========================================================================== */

$component: 'nav-meta';

// block
%#{$component} {
	@include font($ff-regular);
	@include fontSizeREM(13);
	position: relative;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	margin: 0 -30px;
	
	ul,
	.#{$component}--emergency,
	.#{$component}--config-bar	 {
		display: flex;
		margin: 0;
		padding-left: 0;
	}

	li {
		margin: 0;
	}

	a {
		@include font($ff-regular);
		@include fontSizeREM(13);
		padding: 10px $grid-gutter-width;
	}
}


// element


// modifier
.#{$component}--emergency {
	background-color: color('white');
	box-shadow: inset 0 -24px 10px -21px rgba(color('black'), 0.1);
	border-left: solid 1px color('green-light');
	border-right: solid 1px color('green-light');

	& > a {

		&:before {
			@include fontSizeREM(18);
			color: color('red');
		}

		&:hover {
			background-color: color('green-lightest');
			box-shadow: inset 0 -24px 10px -21px rgba(color('black'), 0.1);
			color: color('black');
		}
	}
}

.#{$component}--config-bar {
	background-color: color('ci');
	box-shadow: inset 0 -24px 10px -21px rgba(color('black'), 0.1);

	& > li {
		@include prefix(transition, 'all 0.4s ease');
		padding: 10px;
		color: color('white');

		&:before {
			@include prefix(transition, 'color 0.4s ease');
			@include fontSizeREM(18);
			color: color('white');
			margin: 0;
		}

		&[data-current-lang]:before {
			@include fontSizeREM(13);
			content: attr(data-current-lang);
			text-transform: uppercase;
		}

		& > span[class*="arrow"]:after {
			@include prefix(transition, 'color 0.4s ease');
			@include fontSizeREM(10);
			margin-left: 4px;
		}

		&:hover,
		&.is-open {
			background-color: color('white');
			box-shadow: none;

			&:before {
				color: color('black');
			}

			& > span[class*="arrow"]:after {
				color: color('ci-hover');
			}

			&.is-open > span[class*="arrow"]:after {
				color: color('ci-hover');
				content: icon('arrow-top');
			}
		}

		&.is-open {
			&.util-icon--search {
				background-color: color('ci');
				box-shadow: 3px 3px 8px 1px rgba(color('black'), 0.1);

				&:before {
					color: color('white');
				}
			}
		}
	}

}

/*------------------------------------------------*/
/* FLYOUT STYLES*/
/*------------------------------------------------*/

.#{$component}__flyout--toggle {
	cursor: pointer;
	border-left: solid 1px transparent;
	border-right: solid 1px transparent;

	&[class*="font"].is-open {
		border-left: solid 1px color('black');
		border-right: solid 1px color('black');
		position: relative;

		&:after {
			position: absolute;
			background: color('white');
			content: '';
			top: 38px;
			right: 0;
			width: 52px;
			height: 2px;
			z-index: 6;
		}

		.#{$component}__flyout {
			right: -1px;
		}
	}

	&[class*="lang"].is-open {

		&:after {
			position: absolute;
			background: color('white');
			content: '';
			top: 30px;
			right: 1px;
			width: 52px;
			height: 30px;
			z-index: 6;
		}
	}
}

.#{$component}__flyout {
	box-shadow: -3px 3px 8px 2px rgba(color('black'), 0.1);
	background-color: color('white');
	color: color('black');
	max-height: 0;
	overflow: hidden;
	opacity: 0;
	position: absolute;
	top: 38px;
	right: 0;
	z-index: 5;

	&--content {
		padding: $grid-gutter-width;

		& + & {
			border-top: solid 1px color('black');
		}

		& > a {
			display: block;
			padding: 0;
		}

		& > p {
			@include fontSizeREM(13);
			margin-top: $grid-gutter-width-half;
			margin-bottom: 0;
		}
	}

	.#{$component}__flyout--toggle.is-open & {
		@include prefix(transition, 'max-height 0.4s ease');
		max-height: 100vh;
		opacity: 1;
	}

	&--fontsize {
		width: 250px;

		.#{$component}__flyout--toggle.is-open & {
			border: solid 1px color('black');
		}

		&:hover {
			border: none;
		}
	}

	&--language {
		width: 150px;
	}

	&--search {
		background: color('ci');
		color: color('white');
		display: none;
		left: 0;
		margin-left: calc(50% - 50vw);
		overflow: visible;
		width: 100vw;

		.#{$component}__flyout--content {
			@include respondMin(point('min-lg')) {
				max-width: ($container-max-widths / $body-font-size-px) + em;
				margin: 0 auto;
			}
		}
		
		.#{$component}__flyout--toggle.is-open & {
			padding-bottom: $grid-gutter-width;
			display: block;
		}
	}
}

.#{$component}__search-wrapper {
	position: relative;
	z-index: 2;
}

.#{$component}__search-input {
	width: 100%;
	border: 1px solid color('grey-light');
	box-shadow: 0px 5px 25px 0px color('grey-light');
	padding: 13px 10px;
	z-index: 1;

	@include respondMin(point('min-lg')) {
		box-shadow: 0px 5px 25px 0px rgba(color('black'), .25 );
	}
}

.#{$component}__search-button {
	background: color('white');
	border: 1px solid color('grey-light');
	border-left: none;
	border-radius: 0;
	color: color('accent-turquois');
	margin: 0;
	padding: 0 10px 1px;
	position: absolute;
	right: 0;
	top: 0;

	@include respondMin(point('min-lg')) {
	    border: none;
		padding: 1px 10px 2px;
	}

	&:after {
		@include fontSizeREM(20);
		position: absolute;
		color: color('ci');
		right: 15px;
		top: 10px;

		@include respondMin(point('min-lg')) {
			top: 30px;
			right: 30px;
		}
	}
}

.#{$component}__search-autocomplete {
	background: color('white');
	border: 1px solid color('grey-light');
	border-top: none;
	left: 0;
	opacity: 0;
	position: absolute;
	top: 100%;
	transform: translateY(-3em);
	transition: all 0.3s ease-in-out 0s, visibility 0s linear 0.3s, z-index 0s linear 0.01s;
	visibility: hidden; /* hides sub-menu */
	width: 100%;
	z-index: -1;


	.#{$component}__search-input:focus + & {
		margin-top: -1px;
		opacity: 1;
		transform: translateY(0%);
		transition-delay: 0s, 0s, 0s; /* this removes the transition delay so the menu will be visible while the other styles transition */
		visibility: visible; /* shows sub-menu */
		z-index: 1;
	}

	ul {
		box-shadow: 0px 15px 25px 0px rgba(color('black'), .25 );
		display: block;
		margin: 0;
		overflow-y: auto;
		padding: 0 10px;
	}

	li {
		border-top: 1px solid color('green-light');
		color: color('black');
		cursor: pointer;
		display: block;
		line-height: 48px;
		margin-bottom: 0;
		padding: 0 $grid-gutter-width-half;
		width: 100%;
		
		&:hover {
			background: color('grey-lightest');
			color: color('ci-hover');
		}
	}
}

// component class selector
.#{$component} {
	@extend %#{$component};
}
